import Vue from "vue";
import VueRouter from "vue-router";
import routes from "./routes";

Vue.use(VueRouter);

const router = new VueRouter({
	mode: 'history',//historyurl#history
	routes
});


router.beforeEach((to, from, next) => {
	// console.log("to")
	// console.log(to)


	if (to.meta.requireAuth) { // 
		if (localStorage.getItem('token')) { // vuex.statetoken,

			if (to.meta.needrole){
				console.log(to.meta.roles)
				console.log(localStorage.getItem("type"))
				var roles = to.meta.roles
				var per = localStorage.getItem("type")
				console.log(roles.includes(Number(per)))
				if(roles.includes(Number(per))){

					console.log("")
					next()	//
				}else{
					router.push({path:"/404"})
					// next({path:"/404"}0)	//404
				}
			}
			next()	//

		} else {
			next({
				path: '/login',
				query: {redirect: to.fullPath} // path
			})
		}
	} else {//

		next()	//
		// if(to.meta.roles.includes(quanxian)){
		//
		// 	console.log("")
		//
		// }else{
		// 	router.push({name: 'NotFound'})
		//
		// 	// next({path:"/404"})	//404
		// }
	}
})
export default router;
