
import router from "@/router";
export function inject(service) {
    // request
    //tokentoken
    service.interceptors.request.use(
        (config) => {
            // console.log(config, 'config')
            config.headers['Content-Type'] = 'application/json' // 
            config.headers.Authorization = localStorage.getItem("token")
            config.headers.type = "back"//
            config.headers.userid = localStorage.getItem('userid')
            // console.log(sessionStorage.getItem("token"))
            // token
            return config;
        },
        (error) => {
            Promise.reject(error);
        }
    );
    // response
    service.interceptors.response.use(
        (response) => {
            console.log(response)
            switch (response.data.status) {
                case 900:
                    //token

                    sessionStorage.clear()
                    localStorage.clear()
                    router.replace({ //
                        path: '/login',
                        // path
                        query: { redirect: router.currentRoute.value.fullPath }
                    });
            }
            switch(response.status){

                //token
                case 401:
                    //token
                    sessionStorage.clear()
                    localStorage.clear()
                    router.replace({ //
                        path: '/login',
                        // path
                        query: { redirect: router.currentRoute.fullPath}
                    });
            }
            // 
            return response.data
        },
        (error) => {
            // console.log(JSON.stringify(error))
            switch (error.response.status) {
                case 400: error.message = '(400)';
                    break;
                case 401:

                    error.message = '(401)';
                    sessionStorage.clear()
                    localStorage.clear()
                    router.replace({ //
                        path: '/login',
                        // path
                        query: { redirect: router.currentRoute.fullPath }
                    });
                    break;
                case 900:
                    error.message = '(900)';
                    sessionStorage.clear()
                    localStorage.clear()
                    router.replace({ //
                        path: '/login',
                        // path
                        query: { redirect: router.currentRoute.fullPath }
                    });
                    break;
                case 403: error.message = '(403)';
                    break;
                case 404: error.message = '(404)';
                    break;
                case 408: error.message = '(408)';
                    break;
                case 500: error.message = '(500)';
                    break;
                case 501: error.message = '(501)';
                    break;
                case 502: error.message = '(502)';
                    break;
                case 503: error.message = '(503)';
                    break;
                case 504: error.message = '(504)';
                    break;
                case 505: error.message = 'HTTP(505)';
                    break;
            }

        }
    );
}

export default inject;